import React, {useEffect} from 'react'
import { ArrowBack, AddShoppingCart } from '@mui/icons-material';
import { Link, useNavigate } from 'react-router-dom' 
import {Valor} from '../../lib/collares'



function ProductoG({dataS, addCart, }) {
  const navigate = useNavigate()
  useEffect(() => {
    if(!dataS.id){
      navigate('/Tienda')
    }
  }, [])
  

  return (
    <div className='flex justify-center items-center mobile:block'>
      <div>
      <Link to='/Tienda' >
                <button className='flex p-3 mt-2 justify-center text-left w-[100%] items-center border-2 rounded-b-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                <ArrowBack />
                <h3>Regresar</h3>
                </button>
              </Link>
      </div>
      <div>
        <img src={dataS.imagen} alt="" className='h-[400px] w-[400px] mobile:h-[400px] justify-center items-center '/>
      </div> 
      <div className='justify-start items-center ml-2 p-2 w-[60%] mobile:w-[90%] font-bold mobile:text-center'>
      <div className='text-xl font-semibold'>{dataS.titulo} </div>
      {dataS.precioRebaja ? <div>
        <div className='text-xl text-red-500 line-through' >{`${dataS.precio}  MLC o ${(dataS.precio * Valor).toFixed(2)} CUP  `}</div>
        <div className='text-xl' >{`${dataS.precioRebaja}  MLC o ${(dataS.precioRebaja * Valor).toFixed(2)} CUP  `}</div>
      </div>
      :<div className='text-xl' >{`${dataS.precio}  MLC o ${(dataS.precio * Valor).toFixed(2)} CUP  `}</div>}
      <div className='font-normal'>Descripcion: {dataS.descripcion}</div>
      <div> 
      <div>
                <button onClick={() => addCart(dataS, 1 ) } className='flex p-3 mt-2 justify-center text-left w-[100%] items-center font-bold border-2 rounded-b-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                    <AddShoppingCart className='text-green-500  text-9xl m-2'/>
                <h3 >Añadir Al Carrito</h3>
                </button>
                <Link to='/Carrito' className='flex p-3 mt-2 justify-center text-left w-[100%] items-center font-bold border-2 rounded-b-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                <h3 >Ir Al Carrito</h3>
                </Link>
      </div>
      </div>
      </div>
        
        
    </div>

  )
}

export default ProductoG