import { Close } from '@mui/icons-material'
import React from 'react'
import { Link } from 'react-router-dom'

function Ofertas ({Ocultar,}) {
  return (
    <div className='w-[100%] flex mobile:block justify-center items-center text-center'>
       <Link to='/Tienda' className='font-bold p-2 hover:text-cyan-600 '> !No Olvide Revisar Nuestras Rebajas De Invierno En La Tienda!</Link>
       <Close onClick={()=> Ocultar()} className='border-2  rounded-full hover:bg-cyan-200 animate-bounce duration-500 ease-in-out' />
        </div>
  )
  
}

export default Ofertas