import {  AddCircleOutline, Cancel, Close,ArrowBack, RemoveCircleOutline } from '@mui/icons-material';
import React, {useState} from 'react'
import {Valor} from '../../lib/collares'
import {Link} from 'react-router-dom'

function Carrito({addCart, reduceCart,totalPrecio, removeItemCart ,clearCart,cart ,isEmpty}) {
 

  const CarritoVacio=()=>{
    return (
      <div className='text-center font-bold my-9'>
        El Carrito Esta Vacio
      </div>
    )
  };
  const CarritoLleno =  ({carrito}) =>{
    return (
      <div className='flex p-2 border-b-2 mobile:justify-between w-[350px] justify-center items-center'>
        <img src={carrito.imagen} alt='' className=' w-[100px] h-[100px]'/>
        <div className='block p-2 font-semibold '>
        <div className='text-center'>
          {carrito.titulo}
        </div>
        <div  className='pl-2'> { carrito.precioRebaja 
        ? `Precio: ${ (carrito.precioRebaja).toFixed(2) * Valor } CUP`
         : `Precio: ${ (carrito.precio ).toFixed(2) * Valor } CUP`
         }
        </div>
        <div className='pl-2'>
          { carrito.precioRebaja ? `Total: ${(carrito.precioRebaja * carrito.quantity).toFixed(2) * Valor } CUP` :
           `Total: ${(carrito.precio * carrito.quantity).toFixed(2) * Valor } CUP`}
        </div>
      </div>
      <div className='block items-center justify-center'>
      <div className='flex items-center justify-center'>
          <div div className=' p-1 justify-center   items-center hover:translate-y-1 duration-500'>
            <button type='text' onClick={()=> reduceCart(carrito, 1)} >
              <RemoveCircleOutline/>
            </button>
          </div>
          <div className=' p-1 justify-center items-center'>
          {carrito.quantity}
          </div>
          <div className=' p-1 justify-center  items-center hover:translate-y-1 duration-500'>
            <button type="text" onClick={()=> addCart(carrito, 1)}>
            <AddCircleOutline />
            </button>
          </div>
      </div>
      <div className='flex mt-2 items-center shadow-md rounded-md justify-center hover:translate-y-1 duration-500'>
        <button onClick={()=> removeItemCart(carrito.id)} type="text">
        <div className='block  px-2 '>
        <Cancel className='text-red-400'/>
        <h1 className='font-bold'>Eliminar</h1>
        </div>
        </button>
      </div>
      </div>
      </div>
    )

  }

  if ( isEmpty ) {
  return (
  <div>
    {/* <div>
      <Link to='/Tienda' >
                <button className='flex p-3 mt-2 justify-center   items-center font-semibold border-2 rounded-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                <Close />
                </button>
              </Link>
      </div>   */}
      <div>
      <Link to='/Tienda' >
                <button className='flex p-3 mt-2 justify-center text-left w-[100%] items-center border-2 rounded-b-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                <ArrowBack />
                <h3>Regresar</h3>
                </button>
              </Link>
      </div>
      <CarritoVacio />
  </div>)
} else {
  
  return (
    <div  >
      {/* <div>
      <Link to='/Tienda' >
                <button className='flex p-3 mt-2 justify-center   items-center border-2 rounded-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                <Close />
                </button>
              </Link>
      </div>   */}
      <div>
      <Link to='/Tienda' >
                <button className='flex p-3 mt-2 justify-center text-left w-[100%] items-center border-2 rounded-b-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                <ArrowBack />
                <h3>Regresar</h3>
                </button>
              </Link>
      </div>
      <div className='block'>
        
    <div>{cart.map((carrito, index)=> (
      <div>
        <CarritoLleno carrito={carrito} key={index}/>
      </div>
    )) }</div>
      <div className='m-2 justify-center items-center p-2'>
        <div className='flex font-semibold'>
          <h4>
          Subtotal General:
          </h4>
          <p className='text-center pl-2'>
          {(totalPrecio() * Valor).toFixed(2)} CUP o {(totalPrecio()).toFixed(2)} MLC 
          </p>
        </div>
        <div className='flex font-semibold'>
          <h4>
          Domicilio:
          </h4>
          <p className='text-center pl-2'>
          {(0.6 * Valor).toFixed(2)} CUP o 0.6 MLC  
          </p>
        </div>
        <div className='flex font-semibold'>
          <h4>
          Total Con Domicilio:
          </h4>
          <p className='text-center pl-2'>
          {((totalPrecio()+0.6)* Valor).toFixed(2)} CUP o {(totalPrecio()+ 0.6 ).toFixed(2)} MLC
          </p>
        </div>
        <div>
                <Link  to='/Checkout' className='flex p-3 mt-2 justify-center text-left w-[100%] items-center font-bold border-2 rounded-b-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                <h3 >Ordenar</h3>
                </Link>
                <button onClick={() => clearCart()} type="text" className='flex p-3 mt-2 justify-center text-left w-[100%] items-center font-bold border-2 border-red-200 rounded-b-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>Vaciar Carrito</button> 
      </div>
      </div>
      </div>
    </div>
  )
}
}

export default Carrito