import { Link } from 'react-router-dom';
import {useForm } from 'react-hook-form';

function InfoForm({onSubmit}) {
  const {handleSubmit, formState: {errors}, register }  = useForm();
  

  return (
    <div className='text-center justify-center items-center' >
      <h4 className='px-2 font-bold '>Datos De Envio</h4> 
        <form onSubmit={handleSubmit((datos)=> onSubmit(datos))} >
           <div className='p-2'>
            <label  className='px-2 font-bold '>Nombre</label>
            <input  className='border-2 border-black ml-2' type='text'  {...register('Nombre', {
                required: true,
            })  } />
            {errors.Nombre?.type === "required" && <h1 className='text-center text-red-500'>El Nombre Es Requerido</h1>}
           </div>
           <div className='p-2'>
            <label className='px-2 font-bold ' >Telefono</label>
            <input  className='border-2 border-black ml-2' type='text' {...register('Telefono', {
                required: true,
            }) }/>
            {errors.Telefono?.type === "required" && <h1 className='text-center text-red-500'>El Numero De Telefono Es Requerido</h1>}
           </div>
           <div className='p-2'>
            <label  className='px-2 font-bold '>Dirección</label>
            <input  className='border-2 border-black ml-2' type='text' {...register('Direccion', {
                required: true,
            }) } />
            {errors.Direccion?.type === "required" && <h1 className='text-center text-red-500'>La Direccion Es Requerida</h1>}
           </div>
           <div className='flex mobile:block justify-center items-center mt-3'>
            <Link to='/Carrito' className=' p-1 m-2 mobile:mb-5 justify-center text-center items-center font-bold border-2 rounded-b-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
           Volver Al Carrito
            </Link>
           <input type="submit" value="Siguiente Paso" className=' p-1 m-2 justify-center text-center items-center font-bold border-2 rounded-b-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 ' />
           </div>
        </form>
      </div>
  )
}

export default InfoForm