import React from 'react';
import { Link } from 'react-router-dom';
import servicioimagen from '../images/services.jpg';
import tiendaimagen from '../images/Tienda Invierno.jpg';
import guarderiaimagen from '../images/guarderia.jpg';
import veterinarioImage from '../images/veterinario.jpg';
import entrenadorImage from '../images/entrenador.jpg';
import fumigadorImage from '../images/fumigador2.jpg'

function Home() {
  return (
    <div>
      <div className='block w-full justify-around px-[5px] py-[20px]' >
      <div className='flex flex-1 items-center justify-center' >
      <Link to='/Tienda' className='shadow-2xl w-[50%] m-3 border-2 rounded-3xl h-[25%] overflow-hidden hover:scale-105 duration-300 relative justify-center  items-center '>
        <h2 className='shadow-2xl m-3 mobile:m-1 p-2  absolute text-4xl text-white bg-blue-400 rounded-3xl mobile:text-sm ' >Tienda
        </h2> 
        <img src={tiendaimagen} alt="tienda_imagen" className='box-content z-10 w-[100%] ' />
      </Link >
      <Link to='/Servicios' className='shadow-2xl w-[50%] m-3 border-2 rounded-3xl h-[25%] overflow-hidden hover:scale-105 duration-300 relative justify-center items-center '>
        <h2 className='shadow-2xl m-3 mobile:m-1 p-2  absolute text-4xl text-white bg-blue-400 rounded-3xl  mobile:text-xs' >Servicios
        </h2> 
        <img src={servicioimagen} alt="Servicios_imagen" className='box-content z-10 w-[100%] ' />
      </Link >
      </div>
      <div className='flex flex-row-reverse items-center justify-center' >
      <Link to='Guarderia' className='shadow-2xl w-[60%] m-3 border-2 rounded-3xl  overflow-hidden  hover:scale-105 duration-300 relative items-center justify-center ' >
      <h2 className='shadow-2xl m-3 mobile:m-1 p-2  absolute text-4xl text-white bg-blue-400 rounded-3xl  mobile:text-xs' >Guarderia
        </h2> 
        <img src={guarderiaimagen} alt="guarderia_imagen" className='box-content z-10 w-[100%] h-[48%]' />
      </Link >
      </div>
      </div>
    <div className='shadow-sm rounded-lg'>
      <h1 className='text-center font-bold text-3xl p-3'>Servicios Externos</h1>
    </div>
    <div className='block w-full justify-around px-[5px] py-[20px]' >
      <div className='flex flex-1 items-center justify-center' >
      <Link to='/Veterinario' className='shadow-2xl w-[60%] m-3 border-2 rounded-3xl h-[25%] overflow-hidden hover:scale-105 duration-300 relative justify-center  items-center '>
        <h2 className='shadow-2xl m-3 mobile:m-1 p-2 bottom-0 right-0 absolute text-4xl text-white bg-blue-400 rounded-3xl mobile:text-sm ' >Veterinario
        </h2> 
        <img src={veterinarioImage} alt="tienda_imagen" className='box-content z-10 w-[100%] ' />
      </Link >
      <Link to='/Entrenador' className='shadow-2xl w-[50%] m-3 border-2 rounded-3xl h-[25%] overflow-hidden hover:scale-105 duration-300 relative justify-center items-center '>
        <h2 className='shadow-2xl m-3 mobile:m-1 p-2  absolute text-4xl text-white bg-blue-400 rounded-3xl  mobile:text-xs' >Entrenador
        </h2> 
        <img src={entrenadorImage} alt="Servicios_imagen" className='box-content z-10 w-[100%] ' />
      </Link >
      </div>
      <div className='flex flex-row-reverse items-center justify-center' >
      <Link to='/Fumigador' className='shadow-2xl w-[60%] m-3 border-2 rounded-3xl  overflow-hidden  hover:scale-105 duration-300 relative items-center justify-center ' >
      <h2 className='shadow-2xl m-3 mobile:m-1 p-2 bottom-0 right-0 absolute text-4xl text-white bg-blue-400 rounded-3xl  mobile:text-xs' >Control De Plagas
        </h2> 
        <img src={fumigadorImage} alt="guarderia_imagen" className='box-content z-10 w-[100%] h-[48%]' />
      </Link >
      </div>
      </div>
    </div>
    )
}


export default Home