import React from 'react'
import entrena from '../../images/entrenador.jpg'
import { Link } from 'react-router-dom';
import { ArrowBack } from '@mui/icons-material';

function Entrenador() {
  const texto = 'Buenas. Le Escribo Desde La App El Paraiso Perruno Estoy Interesado en sus servicios'
    return (
        <div>
          <div className='mobile:block text-center font-semibold w-[100%] p-2  ' >
          <div>
          <Link to='/' >
                    <button className='flex p-3 mt-2 justify-center text-left w-[100%] items-center border-2 rounded-b-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                    <ArrowBack />
                    <h3>Regresar</h3>
                    </button>
                  </Link>
          </div>  
          <div className='flex mobile:block text-center font-semibold w-[100%] p-2  '>
          <img src={entrena} alt="Guarderia"  className='text-center w-[350px] rounded-lg m-2'/>
          <div className='block text-center p-5 pl-[40px] mobile:p-1'>
          <h1 className='font-bold text-4xl p-2' >Entrenador</h1>
          <h1 className='font-bold  p-2' >Maestro Victor</h1>
          <p className='text-start'><span className='font-bold'>DESCRIPCIÓN: </span>  Con 35 años de experiencia en la crianza de perros, 20 como Adiestrador Canino; garantizamos la educación y el adiestramiento de su cachorro con la combinación de métodos didácticos y el reforzamiento positivo... buscamos la interrelación con el cliente dándole participación en las formas y métodos de adiestramiento...si es un dueño de mascota principiante  aportaremos conocimientos para el mejor desarrollo en la crianza de su cachorro y si tiene experiencia previa intercambiamos formas adecuadas de crianza...
              </p>
          </div>
          </div>
          </div>
          <a  href={`https://api.whatsapp.com/send?phone=5353734868&text=${texto}`}  className='flex p-3 mt-2 justify-center text-left w-[100%] items-center font-bold border-2 rounded-b-xl mb-3  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                <h3 >Contactar</h3>
                </a>
        </div>
      )
    }

export default Entrenador