import React from 'react'
import fumiga from '../../images/fumigador2.jpg'
import { Link } from 'react-router-dom';
import { ArrowBack } from '@mui/icons-material';

function Fumigador() {
  const texto = 'Buenas. Le Escribo Desde La App El Paraiso Perruno Estoy Interesado en sus servicios'
    return (
        <div>
          <div className='mobile:block text-center font-semibold w-[100%] p-2 m-2 ' >
          <div>
          <Link to='/' >
                    <button className='flex p-3 mt-2 justify-center text-left w-[100%] items-center border-2 rounded-b-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                    <ArrowBack />
                    <h3>Regresar</h3>
                    </button>
                  </Link>
          </div>  
          <div className='flex mobile:block text-center font-semibold w-[100%] p-2 m-2 '>
          <img src={fumiga} alt="Guarderia"  className='text-center w-[280px] rounded-lg m-2'/>
          <div className='block text-center p-5 pl-[40px] mobile:p-1'>
          <h1 className='font-bold text-4xl p-2' >Control De Plagas</h1>
          <p className='text-start'> PRESTAMOS SERVICIO DE CONTROL DE PLAGAS, en casas particulares, en su negocio o en áreas externas...Infestaciones de pulgas, garrapatas, piojos ....hormigas, cucarachas, mosquitos, ratones, babosas, caracoles y otros.....No damos el mismo tratamiento cuando se trata de abejas, avispas, murciélagos y otros para ello priorizamos la preservación de la especie y en consecuencia será el tratamiento...
              </p>
          </div>
          </div>
          </div>
          <a  href={`https://api.whatsapp.com/send?phone=5353734868&text=${texto}`}  className='flex p-3 mt-2 justify-center text-left w-[100%] items-center font-bold border-2 rounded-b-xl mb-3  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                <h3 >Contactar</h3>
                </a>
        </div>
      )
    }

export default Fumigador