import React from 'react'
import Guard from '../../images/guarderia.jpg'
import { Link } from 'react-router-dom';
import { ArrowBack } from '@mui/icons-material';
function Guarderia() {
  const texto = 'Buenas. Estoy Interesado En Su Servicio De Guarderia Canina '
  return (
    <div>
      <div className='mobile:block text-center font-semibold w-[100%] p-2  ' >
      <div>
      <Link to='/' >
                <button className='flex p-3 mt-2 justify-center text-left w-[100%] items-center border-2 rounded-b-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                <ArrowBack />
                <h3>Regresar</h3>
                </button>
              </Link>
      </div>  
      <div className='flex mobile:block text-center font-semibold w-[100%] p-2 m-2 '>
      <img src={Guard} alt="Guarderia"  className='text-center w-[280px] rounded-lg m-2'/>
      <div className='block text-center p-5 pl-[40px] mobile:p-1'>
      <h1 className='font-bold text-4xl p-2' >Servicios Guarderia Canina</h1>
      <p className='text-start'> <span className='font-bold'>Perros Pequeños:</span>  500 CUP Por Noche. <br/>
      <span className='font-bold'> Perros Medianos:</span> 650 CUP Por Noche. <br/>
      <span className='font-bold'>  Perros Grande:</span> 800 CUP Por Noche. <br/>
          Incluye Dos Comidas Al Dia (Arroz, Vianda y Carne),<br/>
          Más De Cuatro Noches, Resivira Un Baño Gratis En Nuestra Estetica.<br/>
          </p>
          <h5 className='text-center'>
          <span className='font-bold text-center '> Condiciones:</span><br/>
          </h5>
          <h5>
          Perros Sanos En Optimas Condiciones De Salud y Con Su Vacunación Al Dia,No Admitimos Perros Con Problemas De Conducta, Agresividad.Todo Esto Es A Criterio. Debera Solicitar El Servicio Como Minimo Con 3 Dias De Antelacion
          </h5>
      </div>
      </div>
      </div>
      <a  href={`https://api.whatsapp.com/send?phone=5355179245&text=${texto}`}  className='flex p-3 mt-2 justify-center text-left w-[100%] items-center font-bold border-2 rounded-b-xl mb-3  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                <h3 >Contactar</h3>
                </a>
    </div>
  )
}

export default Guarderia