import React from 'react'
import { Link } from 'react-router-dom'
import {Valor} from '../../lib/collares'
import rebajaicon from '../../images/pngegg.png'

function CardProduct({productos,getProductsIN,addCart}) {

  

  return (
    <div className='transition-all w-[250px] h-[310px] mt-2 mx-0 mobile:w-[100] mobile:h-[150] '>
      <div className=' m-[5px] pt-5 h-[100%] bg-white relative shadow-xl transition-all   hover:-translate-y-3 duration-500 ' >
        <div className='bg-white block absolute top-0 left-0 w-full h-[50%]'></div>
        <img src={productos.imagen} alt="images" className='w-[100%] h-[50%] hover:scale-150 duration-500 object-contain items-end justify-center bg-no-repeat absolute ' />
        {productos.precioRebaja 
        ? <img src={rebajaicon}  alt='iconrebaja' className=' absolute top-1 left-1 z-[1]  w-12 h-12  cursor-pointer' /> 
        : <div className=' absolute top-5 right-5 z-[1]  w-6 h-6  cursor-pointer'></div> }
        <div className='block  absolute w-full bottom-0 items-end justify-center text-black font-bold '>
            <h1 className='text-[16px]  text-black font-bold items-center justify-center text-center '>{productos.titulo}</h1>
            <p className='text-[10px] items-center font-semibold justify-center text-center'>{productos.descripcion}</p>
            <div className='action flex justify-center items-center p-4 pt-0 mt-1 pb-1   '>
              {productos.precioRebaja 
              ? <div>
                <div className='flex justify-start items-start text-[18px] text-red-500 line-through font-bold text-left '  >{(productos.precio * Valor).toFixed(2) } CUP</div>  
                <div className='flex justify-start items-start text-[18px] text-black font-bold text-left '  >{(productos.precioRebaja * Valor).toFixed(2) } CUP</div>
                </div> 
              : <div className='flex justify-start items-start text-[18px] text-black font-bold text-left '  >{(productos.precio * Valor).toFixed(2) } CUP</div>}
                        
              <div >
                <div >
                <button onClick={()=> addCart(productos,1) } className='flex ml-1 justify-center pl-2 pr-1 mb-1 items-center rounded-3xl border-2 hover:bg-blue-200 hover:-translate-y-1 duration-500 ' >Comprar</button>
                </div>
              <Link to={`/ProductoG/:${productos.id}`} onClick={()=> getProductsIN(productos)} className='flex ml-1 justify-center pl-2 pr-1 items-center rounded-3xl border-2 hover:bg-blue-200 hover:-translate-y-1 duration-500 ' >Saber Más</Link>
              </div>
            </div>
        </div>
      </div>
      
    </div>
  )
}

export default CardProduct