import React from 'react';
import {Button,Badge, Typography} from '@mui/material';
import { ShoppingCartCheckout } from '@mui/icons-material';
import {Link} from 'react-router-dom'
function Navbar({cart}) {
  return (
    <div className='p-3 flex text-white justify-between items-center shadow-xl bg-[#506477]'>
        <div className='mx-2 mobile:text-center'>
            <Typography variant='h4' >El Paraiso Perruno</Typography>
        </div>
        <div className='mx-2'>
          <Link to='/'>
            <Button color='inherit' type='Text'>Home</Button>
          </Link>
          <Link to='/Tienda'>
            <Button color='inherit' type='Text'>Tienda</Button>
          </Link>
          <Link to='/Servicios'>
            <Button color='inherit' type='Text'>Servicios</Button>
          </Link>
          <Link to='/Guarderia'>
            <Button color='inherit' type='Text'>Guarderia</Button>
          </Link>
        </div>
        <div className='mx-2'>
            <Link to='/Carrito'>
            <Badge color='secondary' className='p-1' badgeContent={cart()}>
            <ShoppingCartCheckout />
            </Badge>
            </Link>
        </div> 


    </div>
  )
}

export default Navbar