import { ArrowBack } from '@mui/icons-material';
import { Link } from 'react-router-dom';
import CardProduct from '../Schemas/CardProduct';
import { Filtrado } from '../../Component/helpers/filtrado';
import {useEffect,useState} from 'react'



function Tienda({data,getProductsIN,addCart}) {
  const {filter,filterProduct, filterProductRebaja} = Filtrado(data);
  const [abajo,setAbajo] = useState(false);

  useEffect(() => {
    const onScroll =()=>{
      if(window.scrollY > 300) {
        setAbajo(true)
      }else{
        setAbajo(false)
      }
    } 
    window.addEventListener('scroll', onScroll)
  }, [])


  return (
    <div >
          <div className={abajo ? 'fixed w-full top-1 z-20 ease-out  transition-all ': ''}>
            <div >
            <Link to='/' >
                <button className={`flex p-3 mt-2 justify-center text-left w-[100%] items-center border-2 rounded-b-xl font-semibold hover:bg-blue-200 hover:translate-y-1 duration-500 ${abajo? 'bg-white': ''}`}>
                <ArrowBack />
                <h3>Regresar</h3>
                </button>
              </Link>
            </div>
            <ul  className='flex flex-wrap justify-center items-center font-semibold  '>
              <button onClick={()=> filterProduct()}  className={` p-3 mt-2 mx-1 shadow-md justify-center items-center border-2 rounded-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 ${abajo? 'bg-white': ''}`} >All</button> 
              <button onClick={()=> filterProductRebaja()} className={` p-3 mt-2 mx-1 shadow-md justify-center items-center border-2 rounded-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 ${abajo? 'bg-white': ''}`}>Rebajas</button> 
              <button onClick={()=> filterProduct('Collares')} className={` p-3 mt-2 mx-1 shadow-md justify-center items-center border-2 rounded-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 ${abajo? 'bg-white': ''}`}>Collares</button> 
              <button onClick={()=> filterProduct('Pechera')} className={` p-3 mt-2 mx-1 shadow-md justify-center items-center border-2 rounded-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 ${abajo? 'bg-white': ''}`}>Pecheras</button> 
              <button onClick={()=> filterProduct('Juguete')} className={` p-3 mt-2 mx-1 shadow-md justify-center items-center border-2 rounded-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 ${abajo? 'bg-white': ''}`}>Juguetes</button> 
              <button onClick={()=> filterProduct('Ropa')} className={` p-3 mt-2 mx-1 shadow-md justify-center items-center border-2 rounded-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 ${abajo? 'bg-white': ''}`}>Ropa</button> 
              <button onClick={()=> filterProduct('Comestibles')} className={` p-3 mt-2 mx-1 shadow-md justify-center items-center border-2 rounded-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 ${abajo? 'bg-white': ''}`}>Comestibles</button> 
              <button onClick={()=> filterProduct('Higiene')} className={` p-3 mt-2 mx-1 shadow-md justify-center items-center border-2 rounded-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 ${abajo? 'bg-white': ''}`}>Higiene</button> 
            </ul>
          </div>
          <div className='flex flex-wrap item-center justify-center ease-out transition-all will-change-auto mb-4 z-10'>
            {filter.map((productos,index) =>{
              return (
                <CardProduct productos={productos} addCart={addCart} getProductsIN={getProductsIN} key={index}/>
              )
            })
            }
          </div>
    </div>
  )
}

export default Tienda