import React, { useState } from 'react';
import {Paper, Stepper , Step, StepLabel } from '@mui/material';
import InfoForm  from './InfoForm';
import Review from './Review';

const steps=[ 
  'InfoPersonal',
  'Revision De Datos'
];

function Checkout({totalPrecio, cart,clearCart }) {
  const [activeStep, setActiveStep]= useState(0);
  const [data,setData] = useState({});


  const nextStep = () => setActiveStep((prevActiveStep) => prevActiveStep + 1);
  const backStep = () => setActiveStep((prevActiveStep) => prevActiveStep - 1);
  
  const onSubmit = (datos) =>{
    setData(datos);
    nextStep();
    console.log(data)
  }

  const Confirmation =() =>(
    <div>
        Confirmacion
    </div>
);

const Form = () => activeStep === 0
?<InfoForm  onSubmit={onSubmit}/>
:<Review data={data} backStep={backStep} nextStep={nextStep} clearCart={clearCart} cart={cart} totalPrecio={totalPrecio} />

return (
<>
    <div>
        <main>
            <Paper className='p-3 m-4 justify-center items-center'>
                <h4 className='text-center'>Formulario Para Pedido</h4>
                <Stepper activeStep={activeStep} className='text-center p-1'>
                    {steps.map((step)=>(
                        <Step key={step}  >
                            <StepLabel>{step}</StepLabel>
                        </Step>
                    ))}
                </Stepper>
                {activeStep === steps.length ? <Confirmation /> : <Form  />}
            </Paper>
        </main>
    </div>

</> 
)
}

export default Checkout