import './App.css';
import Navbar from './Component/Schemas/Navbar';
import { HashRouter, Route, Routes } from 'react-router-dom';
import Home from './Component/Home'
import Servicios from './Component/PagesTienda/Servicios'
import Tienda from './Component/PagesTienda/Tienda'
import Carrito from './Component/PagesTienda/Carrito'
import Guarderia from './Component/PagesTienda/Guarderia'
import { useEffect, useState } from 'react';
import { ProductosList } from './lib/collares';
import ProductoG from './Component/Schemas/ProductoG';
import Footer from './Component/Schemas/Footer';
import Checkout from './Component/Checkout/Checkout';
import Fumigador from './Component/PagesTienda/Fumigador';
import Entrenador from './Component/PagesTienda/Entrenador';
import Veterinario from './Component/PagesTienda/Veterinario';
import swal from 'sweetalert2'
import Ofertas from './Component/Schemas/Ofertas';

function App() {
  const [oculto,setOculto] = useState(false);
  const [data,setData] = useState([]);
  const [dataS,setDataS] = useState([]);
  const [cart,setCart] = useState([]);
  
  const Ocultar = () =>{
    setOculto(true);
  };

  const getProducts =  () => {
      setData(ProductosList);
    };
    const getProductsIN =  (ind) => {
      setDataS(ind);
    };
    const totalItems = () => cart.reduce((total, productoactual) => total + productoactual.quantity, 0 )
    const totalPrecio = () => cart.reduce((total, productos) => total + (productos.precioRebaja ?( productos.precioRebaja * productos.quantity):(productos.precio * productos.quantity)), 0 )
    const clearCart = ()=> setCart([]);
    const estaEnCart = (id) => cart.find(product => product.id === id ) ? true : false;
    const removeItemCart = (id) => {
      setCart(cart.filter(product => product.id !== id ))
    }
    const addCart =(item, quantity ) =>{
      if (estaEnCart(item.id)) {
        setCart(cart.map(product => {
          return product.id === item.id ? {...product , quantity: product.quantity + quantity } : product 
        }));
      } else {
        setCart([...cart, {...item, quantity}])
      }
      swal.fire("Añadido!", "El Articulo Ha Sido Añadido Al Carrito!", "success");
    }
    const reduceCart =(item, quantity ) =>{
      if (item.quantity === 1) {
        removeItemCart(item.id)
      } else {
        setCart(cart.map(product => {
          return product.id === item.id ? {...product , quantity: product.quantity - quantity } : product 
        }));
      }
    }
    const isEmpty = (cart.length === 0)? true: false;

  useEffect(() => {
  getProducts();

  }, []);
  console.log(cart);
  return (

    <div>
      <HashRouter >
          {!oculto 
          ?<div  >
            <Navbar cart={totalItems}/>
            <Ofertas Ocultar={Ocultar} data={data} />
          </div>
          : <Navbar cart={totalItems}/> }
          {/* <div className={`fixed top-1 right-1 w-[30%] bg-white border-l-2  z-50 `}>
          
          </div> */}
          <Routes>
              <Route path='/' element={<Home />}></Route>
              <Route path='/Tienda' element={<Tienda data={data} addCart={addCart} getProductsIN={getProductsIN}/>}></Route>
              <Route path='/Servicios' element={<Servicios />}></Route>
              <Route path='/Guarderia' element={<Guarderia />}></Route>
              <Route path='/Fumigador' element={<Fumigador />}></Route>
              <Route path='/Entrenador' element={<Entrenador />}></Route>
              <Route path='/Carrito' element={<Carrito reduceCart={reduceCart} isEmpty={isEmpty}  totalPrecio={totalPrecio} 
          removeItemCart={removeItemCart} clearCart={clearCart} addCart={addCart} cart={cart} />}></Route>
              <Route path='/Veterinario' element={<Veterinario />}></Route>
              <Route path={`/ProductoG/:${dataS.id}`} element={<ProductoG  dataS={dataS} addCart={addCart} />}></Route>
              <Route path='/Checkout' element={<Checkout  totalPrecio={totalPrecio} clearCart={clearCart} cart={cart} />}></Route>
          </Routes>
          <Footer/>
      </HashRouter>
    </div>
  )
}

export default App;
