import { useState } from "react";

export const Filtrado=(data)=> {
    const [filter,setFilter] = useState(data);
    
    const filterProduct = (cat) =>{
    if(!cat){
        return setFilter(data)
    }else{
        const updateList = data.filter((x)=> x.categoria === cat )
        setFilter(updateList);
    };
    }
    const filterProductRebaja = () =>{
    const updateList = data.filter((x)=> x.precioRebaja )
    setFilter(updateList);
    }


    return {filter, filterProduct , filterProductRebaja};
    }
