import React from 'react'
import {Valor} from '../../lib/collares'


function Review({data,backStep,totalPrecio,clearCart,cart}) {
    const articulos = [
        [
        `Lista De Productos: ${[cart.map((prod)=> {
        return [
            `Articulo: ${prod.titulo}`,
            `Cantidad:  ${prod.quantity}`,
            `Subtotal: ${prod.precioRebaja
              ?(prod.precioRebaja * prod.quantity).toFixed(2) * Valor
              :(prod.precio * prod.quantity).toFixed(2) * Valor
            }  CUP`]
    })]}`],[
        `Nombre: ${data.Nombre }`,
        `Telefono: ${data.Telefono}`,
        `Direccion: ${data.Direccion}`
    ],
    [
        `Valor Total Con Domicilio :  ${((totalPrecio()+0.6) * (Valor)).toFixed(2) } CUP o ${(totalPrecio()+ 0.6).toFixed(2) } MLC`
    ]
    ];
return (
    <div  className='items-center justify-center w-[100%]' >
        <div  className='items-center text-center justify-center w-[100%]'>{cart.map((carrito, index)=> (
            <div className='flex p-2 border-b-2 justify-center'>
            <img src={carrito.imagen} alt='' className=' w-[100px] h-[100px]'/>
            <div className='block p-2 font-semibold '>
            <div className='text-center'>
              {carrito.titulo}
            </div>
            <div className='flex p-1 text-center'>
            <div  className='p-1'> 
              <span className='font-bold'>Precio: </span>
              {carrito.precioRebaja
              ?( carrito.precioRebaja).toFixed(2) * Valor  
              :( carrito.precio).toFixed(2) * Valor  
              } CUP
            </div>
            <div className='p-1'>
            <span className='font-bold'> Cantidad: </span>{carrito.quantity}
            </div>
            </div>
            <div className='pl-2 text-center'><span className='font-bold'> Total: </span>
            {carrito.precioRebaja
            ?(carrito.precioRebaja * carrito.quantity).toFixed(2)* Valor
            :(carrito.precio * carrito.quantity).toFixed(2)* Valor
            } CUP</div>
          </div>
          
          </div>
          )) }</div>
          <div className='font-semibold text-center mobile:text-left justify-center border-b-2 mb-1 items-center'>
            <h1 className='font-bold'>Datos Personales</h1>
             <ul>
                <li> <span className='font-bold'>
                Nombre: </span>{data.Nombre}</li>
                <li><span className='font-bold'>
                Telefono: </span>{data.Telefono}</li>
                <li><span className='font-bold'>
                Dirección: </span>{data.Direccion}</li>
             </ul>
          </div>
          <div className=' justify-center items-center '>
        <div className=' font-semibold text-center mobile:text-left justify-center items-center'>
          <h4 className='font-bold '>
          Subtotal General: {(totalPrecio()* Valor).toFixed(2)} CUP o  {(totalPrecio()).toFixed(2)} MLC
          </h4>
        </div>
        <div className='text-center mobile:text-left justify-center items-center '>
          <h4 className='font-bold '>
          Domicilio: {(0.6 * Valor).toFixed(2)} CUP o 0.6 MLC 
          </h4>
        </div>
        <div className=' font-semibold text-center mobile:text-left justify-center items-center'>
          <h4 className='font-bold'>
          Total Con Domicilio: {((totalPrecio()+0.6)* Valor).toFixed(2)} CUP o {(totalPrecio()+ 0.6 ).toFixed(2)} MLC 
          </h4>
        </div>
        <div>
                <a  href={`https://api.whatsapp.com/send?phone=5355179245&text=${articulos}`} onClick={()=> clearCart()} className='flex p-3 mt-2 justify-center text-left w-[100%] items-center font-bold border-2 rounded-b-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>
                <h3 >Ordenar</h3>
                </a>
                <button onClick={() => backStep()} type="text" className='flex p-3 mt-2 justify-center text-left w-[100%] items-center font-bold border-2 border-red-200 rounded-b-xl  hover:bg-blue-200 hover:translate-y-1 duration-500 '>Volver</button> 
      </div>
      </div>
    </div>
    
)
}

export default Review